<template>
  <view :style="{'margin':px(config.mT)+' '+px(config.mL)}">
    <view class="pos-re" :style="{height:imgheights[swiperCurrent]+'rpx;'+'position: relative;'}">
      <swiper class="carousel" circular @change="swiperChange" :style="{height:imgheights[swiperCurrent]+'rpx'}" :autoplay="true">
        <swiper-item v-for="(item, index) in options" :key="index" class="carousel-item"  @tap="to(item)">
          <image :data-index='index' @load="imageLoad" :style="{'borderRadius':px(config.r),height:'100%'}" :src="item.url"/>
        </swiper-item>
      </swiper>
      <!-- 自定义swiper指示器 -->
      <view class="swiper-dots" v-if="options.length>0">
        <text class="num">{{ swiperCurrent + 1 }}</text>
        <text class="sign">/</text>
        <text class="num">{{ options.length }}</text>
      </view>
    </view>
  </view>
</template>

<script>
  export default {
    props: {
      options:{
        type:Array,
        default:[]
      },
      config: Object,
    },
    data() {
      return {
        swiperCurrent:0,
        imgheights:[],
      };
    },
    methods: {
      px(num){
        return uni.upx2px(num*2) + 'px'
      },
      to(item){
        if(!item.value){
          return
        }
        this.customTo(item)
      },
      swiperChange(e) {
        const index = e.detail.current;
        this.swiperCurrent = index;
        // this.titleNViewBackground = this.carouselList[index].background;
      },
      imageLoad: function (e) {
        //获取图片真实宽度
        var imgwidth = e.detail.width,
          imgheight = e.detail.height,
          //宽高比
          ratio = imgwidth / imgheight;
        console.log('图片',e);
        //计算的高度值
        var viewHeight = 750 / ratio;
        var imgheight = viewHeight
        var imgheights = this.imgheights
        //把每一张图片的高度记录到数组里
        imgheights[e.target.dataset['index']] = imgheight;// 改了这里 赋值给当前 index
        this.imgheights = [].concat(imgheights)
      },
    },
    mounted() {
    },
  };
</script>

<style scoped lang="stylus">
  .carousel
    width 100%
    height: 420rpx;
    .carousel-item
      width 100%
      height 100%
      overflow hidden
      box-sizing border-box
    image
      width 100%
      height 100%
  .swiper-dots
    display flex
    position absolute
    left 45rpx
    bottom 15rpx
    width 72rpx
    height 36rpx
    background-image url('data:image/png;base64,')
    background-size 100% 100%
    align-items center
  .num
    width 36rpx
    height 36rpx
    border-radius 50px
    font-size 24rpx
    color #fff
    text-align center
    line-height 36rpx
  .sign
    position absolute
    top 0
    left 50%
    line-height 36rpx
    font-size 12rpx
    color #fff
    transform translateX(-50%)
</style>
